export default {
	name: "stars",
	data() {
		return {
			yellowStar: 0,
			grayStar: 5
		};
	},
	props: {
		innerScore: 0,
		showScore: 0
	},
	created() {
		var tempScore = 0;
		if (this.innerScore != null && this.innerScore != undefined && this.innerScore != '') {
			tempScore = this.innerScore;
		}
		var yellowStar = parseInt(tempScore / 2);
		var grayStar = 5 - yellowStar;

		this.yellowStar = yellowStar;
		this.grayStar = grayStar;
		
	}
